import slidesInfo from './slides-info';

// Chaching needed Dom Elements
const slider = window.document.querySelector('.slider');
const sliderItemsContainer = window.document.querySelector(
  '.slider__items__container'
);
let sliderItems;
let sliderNavDots;
const sliderNav = window.document.querySelector('.slider__nav');
const sliderArrowLeft = window.document.querySelector('.slider__arrow-left');
const sliderArrowRight = window.document.querySelector('.slider__arrow-right');

// Local variable for main module
let prevSlideIndex = 0;
let currentSlideIndex = 0;
const slidesNum = slidesInfo.length;
let timerId;
const AUTOPLAYTIME = 3000;

// create Slides items Elements
const createSlides = () => {
  slidesInfo.forEach((slideItem, index) => {
    sliderItemsContainer.insertAdjacentHTML(
      'beforeend',
      `<div class="slider__item position-absolute start-0 top-0 w-100"
             style="transform: translateX(${index * 100}%)" >
          <img  class="slider__item__image"
                src="${slideItem.imgUrl}"
                alt="item-${index + 1} slider image" />

          <div class="d-none d-sm-block w-75 mx-auto slider__item__info
              position-absolute start-50 translate-middle-x" >
            <div class="bg-gradient bg-opacity-50 container border px-3 bg-light rounded-3" >
              <h2 class="text-center fw-bolder">${slideItem.heading}</h2>
              <p class="fs-6 text-center fw-bold fst-italic">${
                slideItem.text
              }</p>
            </div>
          </div>
        </div>`
    );
  });

  sliderItems = window.document.querySelectorAll('.slider__item');
};

//  create create Indicators Elements
const createIndicators = () => {
  slidesInfo.forEach((_, index) => {
    sliderNav.insertAdjacentHTML(
      'beforeend',
      `<li
      class="slider__nav__dot rounded-circle"
      data-slide-num="${index}"
    ></li>`
    );
  });
  sliderNavDots = window.document.querySelectorAll('.slider__nav__dot');
};

//  Helper Function
const goToSlide = (slideNum) => {
  prevSlideIndex = currentSlideIndex;
  currentSlideIndex = slideNum;

  sliderItems.forEach((slide, index) => {
    slide.style.transform = `translateX(${(index - slideNum) * 100}%)`;
  });
  // eslint-disable-next-line no-use-before-define
  activeDot();
};

const nextSlide = () =>
  currentSlideIndex + 1 === slidesNum
    ? goToSlide(0)
    : goToSlide(currentSlideIndex + 1);

const prevSlide = () =>
  currentSlideIndex === 0
    ? goToSlide(slidesNum - 1)
    : goToSlide(currentSlideIndex - 1);

const activeDot = () => {
  sliderNavDots[prevSlideIndex].classList.remove('active');
  sliderNavDots[currentSlideIndex].classList.add('active');
};

//  Keyboard arrows Events
document.addEventListener('keyup', (e) => {
  switch (e.key) {
    case 'ArrowRight':
      nextSlide();
      break;
    case 'ArrowLeft':
      prevSlide();
      break;
    default:
      break;
  }
});

//  autoplay
const autoPlay = () => (timerId = window.setInterval(nextSlide, AUTOPLAYTIME));

const stopAutoPlay = () => window.clearInterval(timerId);

slider.addEventListener('mouseenter', stopAutoPlay);

slider.addEventListener('mouseleave', autoPlay);

//  mouseDragable Function
const mouseDragable = () => {
  let isDown = false;
  let isDrag = false;
  let startX = 0;
  let endX = 0;

  slider.addEventListener('mousedown', (e) => {
    isDown = true;
    slider.classList.add('slider--active');
    startX = e.pageX;
  });

  slider.addEventListener('mouseleave', () => {
    isDown = false;
    // slider.classList.remove("slider--active");
  });

  slider.addEventListener('mouseup', () => {
    isDown = false;
    // slider.classList.remove("slider--active");
    if (isDrag === true) {
      if (endX - startX > 0) {
        prevSlide();
      } else {
        nextSlide();
      }

      isDrag = false;
    }
  });

  slider.addEventListener('mousemove', (e) => {
    if (!isDown) return;
    e.preventDefault();
    // slider.classList.add("slider--active");
    isDrag = true;
    endX = e.pageX;
  });
};

//  Add Events
// Add click event to generator button to get random quote and display it.
sliderArrowLeft.addEventListener('click', prevSlide);

sliderArrowRight.addEventListener('click', nextSlide);

sliderNav.addEventListener('click', (e) => {
  const clickedItem = e.target;
  if (clickedItem.classList.contains('slider__nav__dot')) {
    const currentDotIndex = Number(clickedItem.dataset.slideNum);
    goToSlide(currentDotIndex);
  }
});

//  Slider Init function
export const sliderInit = () => {
  createSlides();
  createIndicators();
  activeDot();
  autoPlay();
  mouseDragable();
};

